<template>
  <div id="app" :class="typeof weather.main != 'undefined' && weather.main.temp < 16 ? 'warm' : ''">
    <main class="main">
      <section class="searchWrap">
        <input
         class="searchBar"
         type="text"
         placeholder="Enter the region"
         v-model="query"
         @keypress="fetchWeather"
         >
      </section>
      <section class="weatherWrap" v-if="typeof weather.main != 'undefined'">
        <div class="locationBox">
          <div class="location">{{weather.name}}, {{weather.sys.country}}</div>
          <div class="date">{{dateBuilder()}}</div>
        </div>
        <div class="weatherBox">
          <p class="temp">{{Math.round(weather.main.temp)}}°C</p>
          <p class="weather">{{ weather.weather[0].main }}</p>
        </div>
      </section>

    </main>

  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      api_key: 'b7408223c39c8ef55b8a3cff6aa57a5f',
      url_base: 'http://api.openweathermap.org/data/2.5/',
      query: '',
      weather: {}
    }
  },
  methods: {
    fetchWeather (e) {
      if (e.key === 'Enter') {
        fetch(`${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`)
          .then(res => {
            return res.json()
          }).then(this.setResults)
      }
    },
    setResults (results) {
      this.weather = results
    },
    dateBuilder () {
      const d = new Date()
      const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
      const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
      const day = days[d.getDay()]
      const date = d.getDate()
      const month = months[d.getMonth()]
      const year = d.getFullYear()
      return `${day} ${date} ${month} ${year}`
    }
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
}
#app {
  background-image: url("../assets/warm.gif");
  background-size: cover;
  background-position: center;
  transition: 0.4s;
}
#app.warm {
  background-image: url("../assets/cold.gif");
}
.main {
  height: auto;
  min-height: 100vh;
  padding: 5%;
}
.searchWrap {
  margin-top: 60px;
  width: 100%;
  margin-bottom: 30px;
}
.searchWrap .searchBar {
  display: block;
  width: 100%;
  padding: 25px;
  color: #313131;
  font-size: 25px;
  appearance: none;
  border: none;
  background: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 0px 15px 0 15px;
  transition: 0.5s;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
}
.searchWrap .searchBar:focus {
  background-color: rgba(255, 255, 255, 0.7);
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
  border-radius: 15px 0px 15px 0;
  border: none;
}
.locationBox .location {
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.25);
}
.locationBox .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  text-shadow: 1px 3px rgba(0, 0, 0, 0.25);
  font-style: italic;
}
.weatherBox {
 text-align: center;

}
.temp {
  display: inline-block;
  padding: 10px 25px;
  color: white;
  font-size: 100px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
  background-color: rgba(255, 255, 255, 0.25);
  border-radius: 15px;
  margin: 30px 0;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}
.weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  font-style: italic;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.25);
}

</style>
