<template>
  <transition name="modal-fade">
    <div class="modal-backdrop">
      <div class="modal"
        role="dialog"
        aria-labelledby="modalTitle"
        aria-describedby="modalDescription"
      >
        <header
          class="modal-header"
          id="modalTitle"
        >
          <slot name="header">
            <img src="../../assets/modal.png" alt="icon" class="header__img">
          </slot>
          <button
            type="button"
            class="btn-close"
            @click="close"
            aria-label="Close modal"
          >
            x
          </button>
        </header>

        <section
          class="modal-body"
          id="modalDescription"
        >
          <slot name="body">
            Thank you for your subscription
          </slot>
        </section>

        <footer class="modal-footer">
          <button
            type="button"
            class="btn-green"
            @click="close"
            aria-label="Close modal"
          >
            Okay
          </button>
        </footer>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'Modal',
  methods: {
    close () {
      this.$emit('close')
    }
  }
}
</script>
<style>
  .modal-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
  }

  .modal {
    background: #FFFFFF;
    box-shadow: 2px 2px 20px 1px;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
    width: 320px;
  }

  .modal-header,
  .modal-footer {

    display: flex;
    justify-content: center;
  }

  .modal-header {
    position: relative;
    color: #4AAE9B;
    justify-content: center;
  }
.header__img {
    padding: 60px 100px 0;

}
  .modal-footer {
    flex-direction: column;
  }

  .modal-body {
    padding: 20px 10px;
    font-family: "Poppins";
    font-size: 26px;
    font-style: normal;
    font-weight: 600;
    line-height: 39px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(66, 57, 57, 1);

  }

  .btn-close {
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    font-size: 20px;
    padding: 10px;
    cursor: pointer;
    font-weight: bold;
    color: rgba(123, 152, 34, 1);
    background: transparent;
  }
  .btn-close:hover {
    color: rgb(101, 124, 31);
  }
  .btn-close:active {
    color: rgb(80, 95, 33);
  }

  .btn-green {
    font-family: "Poppins";
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(123, 152, 34, 1);
    background: none;
    padding: 15px 50px;
    border: 2px solid rgba(123, 152, 34, 1);
    margin: 0 50px 80px;

  }
  .btn-green:hover {
    color: rgb(102, 126, 30);
    border: 2px solid rgb(102, 126, 30);
    background-color: rgb(247, 242, 234);
  }
  .btn-green:active {
    color: rgb(65, 78, 24);
    border: 2px solid rgb(65, 78, 24);
     background-color: rgb(231, 227, 218);
  }

  .modal-fade-enter,
  .modal-fade-leave-to {
    opacity: 0;
  }

  .modal-fade-enter-active,
  .modal-fade-leave-active {
    transition: opacity .5s ease;
  }
</style>
