<script>
// import Modal1 from './modal/Modal1.vue';
import Slick from 'vue-slick'
export default {
  name: 'catalog',
  components: {
    Slick
    //  Modal1,
  },

  data () {
    return {
      isModal1Visible: false,
      products: [
        {
          id: 1,
          img: require('@/assets/item1.png'),
          name: 'bowl 40 cl - Adelie',
