<script>
import GoogleMap from './GoogleMap.vue'
import Modal1 from './modal/Modal1.vue'
export default {
  name: 'Cart',
  components: {
    GoogleMap,
    Modal1
  },
  data () {
    return {
      isModal1Visible: false,
      products: [
        {
          id: 1,
          img: require('@/assets/item1.png'),
          name: 'bowl 40 cl - Adelie',
          price: 38,
          description: 'A bowl with gentle, airy contours set off by a small foot, with a 22cl and 40cl capacity',
          qty: 1,
          inCart: false
        },
        {
          id: 2,
          img: require('@/assets/item3.png'),
          name: 'Flat coupe plate  21 cm',
          price: 42,
          description: 'Flat bottom for easy training - Foot and slight edges to enhance the culinary..',
          qty: 1,
          inCart: false
        },
        {
          id: 3,
          img: require('@/assets/item2.png'),
          name: 'bc oval cocotte 25cl',
          price: 65,
          description: 'With our oval casserole dish from the Belle Cuisine range in EVE porcelain you can co..',
          qty: 1,
          inCart: false
        },
        {
          id: 4,
          img: require('@/assets/item4.png'),
          name: 'bc oval Adelie 45cl',
          price: 48,
          description: ' With our oval casserole dish from the Belle Cuisine range in EVE porcelain you can co..',
          qty: 1,
          inCart: false
        },
        {
          id: 5,
          img: require('@/assets/item5.png'),
          name: 'bc oval Adelie 25cl',
          price: 35,
          description: 'Flat bottom for easy training - Foot and slight edges to enhance the culinary..',
          qty: 1,
          inCart: false
        }
      ],
      cart: []
    }
  },
  methods: {
    saveTolocalStorageCart () {
      localStorage.removeItem('cart')
      localStorage.setItem('cart', JSON.stringify(this.cart))
    },
    remove (id) {
      for (let index = 0; index < this.cart.length; index++) {
        const carItemId = this.cart[index].id

        if (carItemId === id) {
          this.cart.splice(index, 1)

          for (let i = 0; i < this.products.length; i++) {
            if (carItemId === this.products[i].id) {
              this.products[i].inCart = false

              this.saveTolocalStorageCart()

              const basket = document.querySelector('.cartWrap__full')
              const cartLocalStorage = JSON.parse(localStorage.getItem('cart'))
              basket.innerHTML = cartLocalStorage.length
            }
          }
        }

        // Delete cart if it`s empty
        const cartLocalStorage = JSON.parse(localStorage.getItem('cart'))
        if (cartLocalStorage.length === 0) {
          localStorage.removeItem('cart')
          const basket = document.querySelector('.cartWrap__full')
          basket.style.visibility = 'hidden'
          // console.log.clear();
        }
      }
    },
    clearCart () {
      this.cart = []

      localStorage.removeItem('cart')
      console.clear()

      for (let i = 0; i < this.products.length; i++) {
        this.products[i].inCart = false
      }
      const basked = document.querySelector('.cartWrap__full')
      basked.style.visibility = 'hidden'
    },
    cartLocalStorage () {
      if (localStorage.getItem('cart')) {
        const cartLocalStorage = JSON.parse(localStorage.getItem('cart'))
        console.log(cartLocalStorage)
        return cartLocalStorage
      } else {
        return []
      }
    },
    showModal1 () {
      this.isModal1Visible = true
    },
    closeModal1 () {
      this.isModal1Visible = false
    }
  },
  computed: {
    total () {
      let i = 0
      for (let index = 0; index < this.cart.length; index++) {
        i += this.cart[index].price * this.cart[index].qty
      }
      return i
    }

  },
  mounted () {
    this.cart = this.cartLocalStorage()
    // Check products
    for (let index = 0; index < this.products.length; index++) {
      const product = this.products[index]

      if (localStorage.getItem('cart')) {
        const cartLocalStorage = JSON.parse(localStorage.getItem('cart'))
        const basket = document.querySelector('.cartWrap__full')
        basket.style.display = 'block'
        basket.style.visibility = 'visible'
        basket.innerHTML = cartLocalStorage.length

        for (let i = 0; i < cartLocalStorage.length; i++) {
          const cartitem = cartLocalStorage[i]

          if (product.id === cartitem.id) {
            product.inCart = true
          }
        }
      }
    }
  }
}
</script>
